import React from 'react'


const Searchbar = ({handleSubmit, updateSearch,search})=>{

    return (
    <div>
      <form onSubmit={handleSubmit} className="form-inline">
        <input type="text"  placeholder="search food.." className="form-control mb-2 mr-md-2" id="inlineFormInputName2" value={search} onChange={updateSearch}/>
        <button type="submit" className="btn btn-primary mb-2">Search</button>
      </form>
    </div>
    )};


export default Searchbar;


