import React, {useEffect,useState} from 'react';
import Results from './Results.js'
import Searchbar from './Searchbar.js'
import './App.css';


function App() {
  const [food, setFood] = useState([])
  const [search, setSearch] = useState("")
  const [query,setQuery] = useState("chicken")
  const [isloading, setLoading] = useState(false)
  
  const APP_URL= `https://api.edamam.com/search?q=${query}&app_id=2acf9af2&app_key=4a51638f5ed048ffd648a956f22b257e&from=0&to=10`

const getFood = ()=>{
  setLoading(true)


  fetch(APP_URL)
  .then(response => response.json())
  .then(data => {
    setLoading(false);
    setFood(data.hits);
  
  });

  
}


const handleSubmit = event => {
  
  setQuery(search);

  event.preventDefault();
  setSearch("")
};



const updateSearch = e => {
  setSearch(e.target.value);
};


  useEffect(()=>{
    getFood();
  },[query]);


  return (
    <div className="App">
      <header className="App-header">
        <h1><span role="img" alt="donut">ğŸ•ğŸ£ğŸ¥¤ğŸ”ğŸ¥—ğŸğŸ©</span></h1>
        <Searchbar handleSubmit={handleSubmit} updateSearch={updateSearch} search={search}/>
 
      </header>
      <main>
        <div className="container">
          <div className="row justify-content-md-center"> {isloading?<span className="loader">ğŸ•</span> :<span></span> } </div>
          <div className="row justify-content-md-center">
        {food.map((meal)=><Results key={meal.recipe.label} title={meal.recipe.label} img={meal.recipe.image} ing={meal.recipe.ingredientLines}/>)}
          </div>
        </div>
      </main>
    </div>
  );
}

export default App;
